import React, { useContext, useState } from 'react';
import Axios from 'axios';
import { Redirect, Link } from 'react-router-dom';
import { AuthContext } from '../contexts/auth_context';
import { getApiLink } from '../lib/env.lib';
import Notyf from '../lib/notification';

const VerifyEmail = () => {
  const {
    user_email,
    auth_token,
    user_email_verified,
    setEmailAsVerified,
  } = useContext(AuthContext);
  const [stage, setStage] = useState(0);
  const [code, setCode] = useState('');
  const [disabled, setDisabled] = useState(false);

  function requestVerificationEmail() {
    Axios.post(`${getApiLink()}/api/v1/sendVerificationEmail`, {
      xAuthToken: auth_token,
      email: user_email,
    })
      .then((res) => {
        console.log(res.data);
      })
      .catch((e) => {
        console.log(e.response);
        setStage(0);
      });
  }

  function verifyEmail() {
    Axios.post(`${getApiLink()}/api/v1/verifyEmail`, {
      xAuthToken: auth_token,
      code,
    })
      .then((res) => {
        console.log(res.data);
        setStage(2);
        if (res.data.success) {
          setEmailAsVerified();
          Notyf.success('Your account is now verified.');
        } else {
          Notyf.error(res.data.message);
        }
      })
      .catch((e) => {
        console.log({ e });
        if (e.response) {
          setCode('');
          Notyf.error(e.response.data.error.message);
        }
        setDisabled(false);
      });
  }

  return (
    <>
      {stage !== 2 && user_email_verified === 1 && <Redirect to="/" />}
      <div className="container">
        <div className="row">
          <div className="col-lg-3" />
          <div className="col-lg-6 text-center">
            <div className="custom-card2">
              <div className="p-4 m-4">
                {stage !== 2 && (
                  <>
                    <h4>Verify your email address</h4>
                    <p>
                      You are almost done! A Verification code has been sent to
                      your email address.
                    </p>
                  </>
                )}

                {stage === 0 && (
                  <>
                    <button
                      className="btn btn-primary btn-lg mt-2 mb-4"
                      type="button"
                      onClick={() => {
                        setStage(1);
                        console.log('clicked');
                        requestVerificationEmail();
                      }}
                      disabled={disabled}
                    >
                      Resend verification code
                    </button>
                    <br />
                    <a
                      href="#enter-code"
                      className="small"
                      style={{ color: 'inherit' }}
                      onClick={() => {
                        setStage(1);
                      }}
                    >
                      I already have the code.
                    </a>
                  </>
                )}

                {stage === 1 && (
                  <>
                    <input
                      type="text"
                      className="form-control text-uppercase mr-auto ml-auto text-center"
                      placeholder="Enter the code"
                      style={{
                        width: '250px',
                        fontSize: '20px',
                        fontWeight: '600',
                      }}
                      value={code}
                      onChange={(e) => {
                        setCode(e.target.value);
                      }}
                    />
                    <button
                      className="btn btn-primary mt-3 mb-4"
                      type="button"
                      onClick={() => {
                        setDisabled(true);
                        verifyEmail();
                      }}
                      disabled={
                        !(code.length > 0 && code.length < 10) && disabled
                      }
                    >
                      Verify!
                    </button>
                    <br />
                    <a
                      href="#verify"
                      className="small"
                      style={{ color: 'inherit' }}
                      onClick={() => {
                        setStage(0);
                      }}
                    >
                      Oops! I don&apos;t have the code.
                    </a>
                  </>
                )}

                {stage === 2 && (
                  <>
                    <h3>
                      You&apos;re verified!{' '}
                      <span role="img" aria-label="emoji">
                        ðŸ¥³
                      </span>
                    </h3>
                    <p className="mt-3">
                      Nice one! We were able to verify that{' '}
                      <strong>{user_email}</strong> is your real email address
                      and you are, in fact, NOT a robot. Whew!
                    </p>

                    <Link to="/" className="btn btn-primary" as="button">
                      Go to Home page
                    </Link>
                  </>
                )}
              </div>
            </div>
          </div>
          <div className="col-lg-3" />
        </div>
      </div>
    </>
  );
};

export default VerifyEmail;
